export const emptyBoard = Array(81).fill(undefined).map(() => ({ expected: 0, current: { type: "empty" }, notes: [] }));
export const mapSquareAtCoords = ({ x, y }, squares, mapper) => squares.map((square, index) => (y * 9 + x) === index ? mapper(square) : square);
export const zipBoardWithCoords = (squares) => squares.map((square, index) => ({ coords: { x: index % 9, y: Math.floor(index / 9) }, square }));
export const coordsAreEqual = (coords1, coords2) => coords1.x === coords2.x && coords1.y === coords2.y;
